import '../../styles/fish.scss'
import '../../styles/font.scss'
import $ from 'jquery'
import React, { useState } from 'react'
import Swal from 'sweetalert2'

function Forget(props) {
  const [email, setEmail] = useState('')
  async function ForgetToSever() {
    $('#email').next().text('')
    const url = 'http://localhost:4000/forget'
    const request = new Request(url, {
      method: 'POST',
      body: JSON.stringify({ email }),
      credentials: 'include',
      headers: new Headers({
        Accept: 'application/json',
        'Content-Type': 'application/json',
      }),
    })
    // console.log('body : ' + JSON.stringify(email))
    const response = await fetch(request, { mode: 'cors' })
    const data = await response.json()
    console.log('json', data)
    if (data.code === 0) {
      $('#email').next().text(data.update)
    } else
      Swal.fire({
        title: '',
        icon: 'success',
        type: '',
        text: '.',
      })
  }

  return (
    <>
      <div className="fish-forgetbg">
        <div className="container h-100 member-mg">
          <div className="row justify-content-center">
            <div className="fish-mask d-flex justify-content-center col-xl-6 col-lg-8 col-md-10 col-sm-12">
              <form action="" className="pub-form w-80">
                <h3></h3>
                <div className="forget-text">
                  <h6>E-amil</h6>
                  <h6></h6>
                </div>
                <label htmlFor="email "></label>
                <br />
                <input
                  className="w-100 member-input"
                  type="text"
                  name="email"
                  id="email"
                  value={email}
                  onChange={(event) => {
                    setEmail(event.target.value)
                  }}
                />

                <small></small>
                <div className="form-border w-100"></div>

                <button
                  type="button"
                  className="member-button mx-auto"
                  onClick={() => {
                    ForgetToSever()
                  }}
                >
                  
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default Forget
