import '../../styles/fish.scss'
import '../../styles/font.scss'
import React, { useEffect, useState } from 'react'
import FishAside from '../../components/FishAside'
import { Link, withRouter } from 'react-router-dom'
import $ from 'jquery'
import Swal from 'sweetalert2'

function Mpassword(props) {
  const [password, setPassword] = useState('')
  const [newpassword, setNewPassword] = useState('')
  const [newpassword2, setNewPassword2] = useState('')
  const token = sessionStorage.getItem('mid')
  useEffect(() => {
    if (sessionStorage.getItem('mid') === null) {
      props.history.push('/member/login')
    }
  }, [])

  async function Editpassword() {
    if (password.length) {
      $('#password').next().text('')

      if (newpassword.length >= 6) {
        $('#newpassword').next().text('')
        if (newpassword === newpassword2) {
          $('#newpassword2').next().text('')
          const url = 'http://localhost:4000/editpassword'
          const request = new Request(url, {
            method: 'PUT',
            // body: editform,
            body: JSON.stringify({ password, newpassword, token }),

            headers: new Headers({
              Accept: 'application/json',
              'Content-Type': 'application/json',
            }),
          })
          const response = await fetch(request)
          const data = await response.json()
          console.log(' ', data)
          if (data.update === false) {
            console.log('123')

            $('#passwordsmall').text('')
          }
          if (data.update === true) {
            $('#passwordsmall').text('')
            Swal.fire({
              title: '',
              icon: 'success',
              type: '',
              text: '',
            })
          }
        } else {
          $('#newpassword2').next().text('')
        }
      } else {
        $('#newpassword').next().text('')
      }
    } else {
      $('#password').next().text('')
    }
  }
  return (
    <>
      <div className="container h-100">
        <div className="row">
          <div className="col-12 bread-div">
            <a className="clbread" href="*">
               <span className="clspan">{'>'}</span>
            </a>
            <a className="clbread" href="*">
              {' '}
               <span className="clspan">{'>'}</span>
            </a>
            <a className="clbread" href="*">
              {' '}
               <span className="clspan">{'>'}</span>
            </a>
            <a className="clbread" href="*">
              {' '}
            </a>
          </div>
        </div>
        <div className="row justify-content-center">
          <div className="col-2"></div>
          <div className="col-9">
            <h2 className="fish-topic">
              <i className="fas fa-user-edit"> </i> 
            </h2>
          </div>
        </div>
        <div className="row justify-content-center">
          <FishAside />
          <div className="col-9">
            <div className="w-100 d-flex justify-content-center zoomer delay-3">
              <form
                action=""
                className="pub-form col-xl-7 col-lg-8 col-md-10 col-sm-12"
              >
                <label htmlFor="password"></label>
                <br />
                <input
                  className="w-100 pub-input"
                  type="password"
                  name="password"
                  id="password"
                  value={password}
                  onChange={(event) => {
                    setPassword(event.target.value)
                  }}
                />
                <small id="passwordsmall"></small>
                <label htmlFor="newpassword"></label>
                <br />
                <input
                  className="w-100 pub-input"
                  type="password"
                  name="newpassword"
                  id="newpassword"
                  value={newpassword}
                  onChange={(event) => {
                    setNewPassword(event.target.value)
                  }}
                />
                <small></small>

                <label htmlFor="newpassword2"></label>
                <br />
                <input
                  className="w-100 pub-input"
                  type="password"
                  name="newpassword2"
                  id="newpassword2"
                  value={newpassword2}
                  onChange={(event) => {
                    setNewPassword2(event.target.value)
                  }}
                />
                <small></small>

                <button
                  type="button"
                  className="pub-button pub-mb-100 mx-auto"
                  onClick={() => {
                    Editpassword()
                  }}
                >
                  
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div className="bgup-img"></div>
      <div className="bgdown-img"></div>
    </>
  )
}

export default withRouter(Mpassword)
