import { Link } from 'react-router-dom'

function CartCate(props) {
  const { cartStep, setCartStep, cartCate, setCartCate } = props

  return (
    <>
      <div className="row hhyy-cate hy-bg mt-5 p-3">
        <div className="col-12 text-center pb-3"></div>
        <div
          className={
            'col-4 text-center hy-bg-opacity hoyu-a ' +
            (cartCate === 'cart1' ? 'hy-cartmode-active' : '')
          }
        >
          <Link
            className={'hoyu-a w-100 d-block p-2'}
            to="/cart"
            onClick={() => {
              setCartCate('cart1')
              setCartStep('step1')
            }}
          >
            
          </Link>
        </div>
        <div
          className={
            'col-4 text-center hy-bg-opacity hoyu-a ' +
            (cartCate === 'cart2' ? 'hy-cartmode-active' : '')
          }
        >
          <Link
            className="hoyu-a w-100 d-block p-2"
            to="/cart"
            onClick={() => {
              setCartCate('cart2')
              setCartStep('step1')
            }}
          >
            
          </Link>
        </div>
        <div
          className={
            'col-4 text-center hy-bg-opacity hoyu-a ' +
            (cartCate === 'cart3' ? 'hy-cartmode-active' : '')
          }
        >
          <Link
            className="hoyu-a w-100 d-block p-2"
            to="/cart"
            onClick={() => {
              setCartCate('cart3')
              setCartStep('step1')
            }}
          >
            
          </Link>
        </div>

        {/* <ul className="list-unstyled d-flex">
            <li className="p-3"></li>
            <li className="p-3"></li>
            <li className="p-3"></li>
          </ul> */}
      </div>
    </>
  )
}

export default CartCate
