import React, { useEffect, useState } from 'react'
import { Navbar, Nav, NavDropdown } from 'react-bootstrap'
// active cssNavLink
import { FaSearch } from 'react-icons/fa'
import { FaUserAlt } from 'react-icons/fa'
import { FaShoppingCart } from 'react-icons/fa'
import { NavLink, withRouter, Link } from 'react-router-dom'
import '../styles/navbar.scss'
import $ from 'jquery'

function MyNavbar(props) {
  const [test, setTest] = useState(false)
  async function logout() {
    const url = 'http://localhost:4000/logout'
    const request = new Request(url, {
      method: 'POST',
      credentials: 'include',
    })
    const response = await fetch(request)
    const rows = await response.json()
    if (rows.logout === true) {
      sessionStorage.removeItem('mid')
      setScCartShow(false)
      setScCartTotal(0)
      props.history.push('/member/login')
    }
  }
  useEffect(() => {
    let last = 0
    $(window).scroll(function () {
      let scrollNow = $(this).scrollTop()
      if ($(window).scrollTop() >= 0) {
        $('.navbar').removeClass('hide')
      }
      if ($(window).scrollTop() >= 1) {
        $('.navbar').addClass('nav-position ')

        if (scrollNow > last) {
          $('.navbar').addClass('hide')
        } else {
          $('.navbar').removeClass('hide')
        }
      } else {
        $('.navbar').removeClass('nav-position')
      }

      last = scrollNow
    })
  }, [])

  // 

  const {
    scCartTotal,
    setScCartTotal,
    scChange,
    setScChange,
    scCartShow,
    setScCartShow,
  } = props
  const [scCartItems, setScCartItems] = useState([])
  const [scCartItemsQ, setScCartItemsQ] = useState(0)
  const [scCartActivity, setScCartActivity] = useState([])
  const [scCartActivityQ, setScCartActivityQ] = useState(0)
  const [scCartStudio, setScCartStudio] = useState([])
  const [scCartStudioQ, setScCartStudioQ] = useState(0)
  async function fetchCartItems() {
    const res = await fetch('http://localhost:4000/cart1items', {
      credentials: 'include',
    })
    res.json().then((res) => {
      if (res.length === 0) {
        setScCartItems(res)
        setScCartItemsQ(0)
      } else {
        setScCartItems(res)
        let newQuantity = 0
        res.map((item, i) => {
          newQuantity += +item.quantity
        })
        console.log('123')

        setScCartItemsQ(newQuantity)
      }
    })
  }

  async function fetchCartActivity() {
    const res = await fetch('http://localhost:4000/cartActivityItems', {
      credentials: 'include',
    })
    res.json().then((res) => {
      if (res.length === 0) {
        setScCartActivity(res)
      } else {
        console.log('456')

        setScCartActivity(res)
        let newQuantity = 0
        res.map((item, i) => {
          newQuantity += +item.quantity
        })
        setScCartActivityQ(newQuantity)
      }
    })
  }

  async function fetchCartStudio() {
    const res = await fetch('http://localhost:4000/cartStudioItems', {
      credentials: 'include',
    })
    res.json().then((res) => {
      if (res.length === 0) {
      } else {
        setScCartStudio(res)
        let newPrice = 0
        res.map((item, i) => {
          newPrice += +item.price
        })
        setScCartStudioQ(res.length)
        console.log('789')

        // console.log(newPrice)
        // console.log(newQuantity)
      }
    })
  }

  // useEffect(() => {
  //   if (sessionStorage.getItem('mid')) {
  //     fetchCartItems()
  //     fetchCartActivity()
  //     fetchCartStudio()
  //     console.log(':', 1, ':', 2, ':', 3)
  //   }
  // }, [])
  useEffect(() => {
    if (sessionStorage.getItem('mid')) {
      fetchCartItems()
      fetchCartActivity()
      fetchCartStudio()
      console.log(':', 4, ':', 5, ':', 6)
      let scTotal = scCartItemsQ + scCartActivityQ + scCartStudioQ
      setScCartTotal(scTotal)
    }
  }, [scChange])

  useEffect(() => {
    let scTotal = scCartItemsQ + scCartActivityQ + scCartStudioQ
    setScCartTotal(scTotal)
    // scCartItemsQ, scCartActivityQ, scCartActivityQ
  }, [scCartItemsQ])

  useEffect(() => {
    let scTotal = scCartItemsQ + scCartActivityQ + scCartStudioQ
    setScCartTotal(scTotal)
  }, [scCartActivityQ])

  useEffect(() => {
    let scTotal = scCartItemsQ + scCartActivityQ + scCartStudioQ
    setScCartTotal(scTotal)
  }, [scCartStudioQ])

  return (
    <>
      <Navbar
        className="alex-navbarHeight"
        collapseOnSelect
        expand="lg"
        variant="dark"
        id="navbar"
        // fixed="top"
      >
        <Navbar.Brand as={NavLink} to="/" className="nav-left">
          <img src="../icons/icons-row.svg" alt="" className="nav-icons" />
        </Navbar.Brand>
        <di className="nav-right-icons  ">
          <Nav.Link href="#deets">
            <FaSearch />
          </Nav.Link>
          <Nav.Link eventKey={2} as={NavLink} to="/cart">
            <FaShoppingCart />
          </Nav.Link>
        </di>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto nav-between">
            {/* aslink */}
            {/* https://react-bootstrap.github.io/components/navs/#nav-link-props */}
            <Nav.Link className="alex-padding" as={NavLink} to="/">
              
            </Nav.Link>
            <Nav.Link className="alex-padding" as={NavLink} to="/mainproduct">
              
            </Nav.Link>
            <Nav.Link className="alex-padding" as={NavLink} to="/campaign">
              
            </Nav.Link>

            <Nav.Link
              className="alex-padding"
              as={NavLink}
              to="/studioRent/studioOrder"
            >
              
            </Nav.Link>
            <Nav.Link className="alex-padding" as={NavLink} to="#">
              
            </Nav.Link>
            <Nav.Link className="alex-padding" as={NavLink} to="#">
              
            </Nav.Link>
            <Nav className="nav-between nav-member alex-padding">
              <NavDropdown.Divider />

              <NavDropdown title="" id="nav-member">
                <NavDropdown
                  title=""
                  id="nav-member"
                  className="nav-member-link"
                >
                  <NavDropdown.Item
                    as={NavLink}
                    to="/member/edit"
                    className="nav-ul "
                  >
                    
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    as={NavLink}
                    to="/member/editpassword"
                    className="nav-ul"
                  >
                    
                  </NavDropdown.Item>
                </NavDropdown>
                <NavDropdown
                  title=""
                  id="nav-member"
                  className="nav-member-link"
                >
                  <NavDropdown.Item
                    as={NavLink}
                    to="/member/fav-product"
                    className="nav-ul"
                  >
                    
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    as={NavLink}
                    to="/member/fav-active"
                    className="nav-ul"
                  >
                    
                  </NavDropdown.Item>
                </NavDropdown>
                <NavDropdown
                  title=""
                  id="nav-member"
                  className="nav-member-link"
                >
                  <NavDropdown.Item
                    as={NavLink}
                    to="/member/order-product"
                    className="nav-ul"
                  >
                    
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    as={NavLink}
                    to="/member/order-active"
                    className="nav-ul"
                  >
                    
                  </NavDropdown.Item>
                  <NavDropdown.Item
                    as={NavLink}
                    to="/member/order-class"
                    className="nav-ul"
                  >
                    
                  </NavDropdown.Item>
                </NavDropdown>
              </NavDropdown>
              {sessionStorage.getItem('mid') ? (
                <NavDropdown.Item
                  id="logout"
                  onClick={() => {
                    logout()
                  }}
                >
                  <NavDropdown.Divider />

                  <p></p>
                </NavDropdown.Item>
              ) : (
                <NavDropdown.Item as={NavLink} to="/member/login">
                  <NavDropdown.Divider />

                  <p></p>
                </NavDropdown.Item>
              )}
            </Nav>
          </Nav>

          <Nav className="nav-right">
            <Nav.Link href="#deets">
              <FaSearch />
            </Nav.Link>
            <NavDropdown title={<FaUserAlt />}>
              <NavDropdown.Item
                as={NavLink}
                to="/member/edit"
                className="drop-border"
              >
                <p></p>
              </NavDropdown.Item>
              <NavDropdown.Divider />
              {sessionStorage.getItem('mid') ? (
                <NavDropdown.Item
                  id="logout"
                  onClick={() => {
                    logout()
                  }}
                >
                  <p></p>
                </NavDropdown.Item>
              ) : (
                <NavDropdown.Item as={NavLink} to="/member/login">
                  <p></p>
                </NavDropdown.Item>
              )}
            </NavDropdown>

            <Nav.Link
              eventKey={2}
              className="position-relative"
              onClick={() => {
                setScCartShow(!scCartShow)
              }}
            >
              <FaShoppingCart />

              <span className="badge badge-pill badge-info  hy-cart-count ">
                {scCartTotal}
              </span>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  )
}

export default withRouter(MyNavbar)
