import '../../styles/fish.scss'
import '../../styles/font.scss'
import { withRouter } from 'react-router-dom'
import React, { useState, useEffect } from 'react'
import $ from 'jquery'
import Swal from 'sweetalert2'

function Register(props) {
  const [inputs, setInputs] = useState({
    username: '',
    email: '',
    password: '',
    account: '',
  })
  const onChangeForField = (fieldName) => (event) => {
    setInputs((state) => ({ ...state, [fieldName]: event.target.value }))
  }
  async function RegisterToSever() {
    if (
      inputs.email.match(
        /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i
      )
    ) {
      $('#email').next().text('')
      $('#account').next().text('')
      $('#password').next().text('')
      const url = 'http://localhost:4000/register'
      const registerform = new FormData(document.querySelector('#registerform'))
      const request = new Request(url, {
        method: 'POST',
        body: registerform,
      })
      const response = await fetch(request)
      const data = await response.json()
      console.log('json', data)
      if (data.register === true) {
        Swal.fire({
          title: '',
          icon: 'success',
          type: '',
          text: '.',
        })
      }
      if (data.code === 1) {
        $('#account').next().text('')
      }
      if (data.code === 2) {
        $('#email').next().text('')
      }
      if (data.code === 3) {
        $('#password').next().text('')
      }
    } else {
      $('#email').next().text('')
    }
  }
  useEffect(() => {
    let curs = document.querySelector('.cursor')

    document.addEventListener('mousemove', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      curs.style.top = y - 22 + 'px'
      // console.log('123')
    })

    document.addEventListener('mouseleave', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      curs.style.top = y - 22 + 'px'
    })
  }, [])
  return (
    <>
      <div class="cursor"></div>

      <div className="fish-registerbg">
        <div className="container h-100 member-mg">
          <div className="row justify-content-center">
            <div className="fish-mask d-flex justify-content-center col-xl-6 col-lg-8 col-md-10 col-sm-12">
              <form action="" className="pub-form w-80" id="registerform">
                <h3></h3>
                <label htmlFor="account "></label>
                <br />
                <input
                  className="w-100 member-input"
                  type="text"
                  name="account"
                  id="account"
                  value={inputs.account}
                  onChange={onChangeForField('account')}
                />

                <small></small>

                <label htmlFor="password"></label>
                <br />
                <input
                  className="w-100 member-input"
                  type="text"
                  name="password"
                  id="password"
                  value={inputs.password}
                  onChange={onChangeForField('password')}
                />
                <small></small>

                <label htmlFor="username"></label>
                <br />
                <input
                  className="w-100 member-input"
                  type="text"
                  name="username"
                  id="username"
                  value={inputs.username}
                  onChange={onChangeForField('username')}
                />
                <small></small>

                <label htmlFor="email"></label>
                <br />
                <input
                  className="w-100 member-input"
                  type="text"
                  name="email"
                  id="email"
                  value={inputs.email}
                  onChange={onChangeForField('email')}
                />
                <small></small>
                <div className="form-border w-100"></div>
                <button
                  type="button"
                  className="member-button mx-auto"
                  onClick={() => {
                    RegisterToSever()
                  }}
                >
                  
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default withRouter(Register)
