import '../../styles/MainProductDetail.scss'
import '../../styles/font.scss'
import { MdFavoriteBorder } from 'react-icons/md'
import { MdFavorite } from 'react-icons/md'
import { MdRemove } from 'react-icons/md'
import { MdAdd } from 'react-icons/md'
import { MdLocalBar } from 'react-icons/md'
import { GiCakeSlice } from 'react-icons/gi'
import { GiBerriesBowl } from 'react-icons/gi'
// import ProductCarousel from '../../components/ProductCarousel'
// import ProductAccordion from '../../components/ProductAccordion'
import { useState, useEffect } from 'react'
import CarouselShow from '../../components/CarouselShow'
import { Link, withRouter } from 'react-router-dom'
import { Carousel } from 'react-bootstrap'
import { Modal, Button, Accordion, Card } from 'react-bootstrap' //import react-bootstrap
import $ from 'jquery'

function MainProductDetail(props) {
  //idconsole.log('url', props.match.params.id)
  const [product, setProduct] = useState([])
  const [carouselImg, setCarouselImg] = useState([])
  const mid = sessionStorage.getItem('mid')
  const [favstate, setFavstate] = useState(false)

  // 
  const { scChange, setScChange } = props

  useEffect(() => {
    const FetchData = async () => {
      // var formData = new FormData()
      // formData.set('productSid', props.match.params.id)
      const url = 'http://localhost:4000/mainproductdetail' //nodeapp.get('/mainproduct')
      const request = new Request(url, {
        method: 'POST',
        credentials: 'include',

        // body: formData,
        body: JSON.stringify({
          productSid: props.match.params.id,
          mid: mid,
        }),
        headers: new Headers({
          Accept: 'application/json',
          'Content-Type': 'application/json',
        }),
      })
      const response = await fetch(request)
      const rows = await response.json() //rows
      console.log('', rows) //console.logrows
      console.log('props.match.params.id', props.match.params.id) //console.logrows
      if (rows[0].fav_id === parseInt(props.match.params.id)) {
        setFavstate(true)
      }
      setProduct(rows) //rowsproduct
      setCarouselImg(rows[0].p_carousel_img.split(','))
      //console.log('rows[0]', rows[0])
      //rows[0].p_carousel_img.split(',')carouselImgcarouselImgmap
    }

    let curs = document.querySelector('.cursor2')

    document.addEventListener('mousemove', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      curs.style.top = y - 22 + 'px'
      // console.log('123')
    })

    document.addEventListener('mouseleave', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      // curs.style.opacity = 0.5
      curs.style.top = y - 22 + 'px'
    })

    FetchData()
  }, [props.match.params.id]) //componentDidMount+componentDidUpdateMainProductDetail[props.match.params.id]componentDidUpdate

  const [total, setTotal] = useState(1)

  //
  const addfavproduct = async (p_sid) => {
    //
    console.log('234')
    const url = 'http://localhost:4000/addfavproduct'
    const request = new Request(url, {
      method: 'POST',
      body: JSON.stringify({ p_sid: p_sid, mid: mid }),
      headers: new Headers({
        Accept: 'application/json',
        'Content-Type': 'application/json',
      }),
    })
    const response = await fetch(request)
    const data = await response.json()
    console.log(' : ', data)
    if (data.fav === true) {
      setFavstate(true)
    } else {
      setFavstate(false)
    }
  }
  // 
  const addToCart = async (id, quantity) => {
    const response = await fetch('http://localhost:4000/AddToCart1', {
      method: 'POST',
      body: JSON.stringify({
        p_sid: id,
        quantity: quantity,
        token: mid,
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    })
    let cartresult = await response.json()
    console.log('cartresult', cartresult)
    if (cartresult === 'error') {
      alert('Something Went Wrong')
    } else {
      setScChange(scChange + 1)
      handleShow()
      // alert('')
    }
  }

  const [modalShow, setModalShow] = useState(false)
  const handleClose = () => setModalShow(false)
  const handleShow = () => setModalShow(true)
  const messageModal = (
    <Modal
      contentClassName="hy-modal"
      show={modalShow}
      onHide={handleClose}
      size="md"
      aria-labelledby="contained-modal-title-vcenter"
      keyboard={false}
      backdrop="static"
      centered
      onClick={() => {
        handleClose()
      }}
    >
      <Modal.Header>
        <Modal.Title id="contained-modal-title-vcenter">
          <h5></h5>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h4></h4>
        <p>!</p>
      </Modal.Body>
      <Modal.Footer>
        <Button
          onClick={() => {
            handleClose()
          }}
        >
          
        </Button>
      </Modal.Footer>
    </Modal>
  )

  return (
    <>
      {messageModal}
      <div className="content container">
        <div className="row k-mp-upper">
          <div className="col breadcrumbs">
            <Link className="clbread" to="/MainPage">
              {'  '}
              <span className="clspan">{'>'}</span>
            </Link>
            <Link className="clbread" to="/mainproduct">
              {'  '}
              <span className="clspan">{'>'}</span>
            </Link>
            <Link className="clbread" to="/mainproduct">
              {'  '}
              <span className="clspan">{'>'}</span>
            </Link>
            <Link className="clbread" to="">
              
            </Link>
          </div>
        </div>

        <div className="row k-mp-lower">
          <div className="col-lg-6 k-left-col">
            <Carousel>
              {carouselImg.map((v, i) => (
                <Carousel.Item key={i}>
                  <img
                    className="d-block w-100"
                    src={`http://localhost:3000/k-images/${v}`}
                    alt="First slide"
                  />
                  <Carousel.Caption></Carousel.Caption>
                </Carousel.Item>
              ))}
            </Carousel>
          </div>

          {product.map((v, i) => (
            <div className="col-lg-6 right-col">
              <div className="d-flex justify-content-between align-items-center">
                <h3 key={i} className="k-product-name">
                  {v.p_name}
                </h3>

                {/* <Link to="" className="a-right-style"> */}
                <i
                  className="k-collection far fa-heart"
                  onClick={() => {
                    addfavproduct(v.p_sid)
                  }}
                >
                  {favstate ? <MdFavorite /> : <MdFavoriteBorder />}
                  {/* favstatetrue */}
                </i>
                {/* </Link> */}
              </div>

              <h4 className="k-mp-product-price">$ {v.p_price}</h4>

              <div className="row shopping-zone">
                <div className="col-lg-6">
                  <div className="k-counter d-flex justify-content-between align-items-center my-3">
                    <button
                      className="counterBtn"
                      onClick={() => {
                        setTotal(total - 1)
                      }}
                    >
                      <MdRemove />
                    </button>
                    <span className="k-number">{total}</span>
                    <button
                      className="counterBtn"
                      onClick={() => {
                        setTotal(total + 1)
                      }}
                    >
                      <MdAdd />
                    </button>
                  </div>
                </div>
                <div className="col-lg-6 my-2">
                  <button
                    className="k-style-addToCartBtn addToCartBtn"
                    onClick={() => {
                      addToCart(v.p_sid, total)
                    }}
                  >
                    
                  </button>
                </div>
              </div>

              <h5 className="product-description-text">{v.p_desc}</h5>
            </div>
          ))}
        </div>

        <Accordion defaultActiveKey="0">
          <Card>
            <Accordion.Toggle as={Card.Header} eventKey="0">
              <div className="info-head d-flex justify-content-between d-flex align-items-center">
                <h5 className="k-note"></h5>
                <MdAdd />
              </div>
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="0">
              <Card.Body>
                <div className="info-body">
                  <div className="k-xd-icon d-flex justify-content-between">
                    <span className="k-material-icons material-icons">
                      <MdLocalBar />
                    </span>
                    <span className="k-material-icons material-icons">
                      <GiBerriesBowl />
                    </span>
                    <span className="k-material-icons material-icons">
                      <GiCakeSlice />
                    </span>
                  </div>
                  {product.map((v, i) => (
                    <h5 key={i} className="info-body-text text-center">
                      
                      <span>{v.Alcohol_vol}% </span>
                      <br />
                      <span>{v.ingredient}</span>
                      <br />
                      
                      <br />
                      
                      <br />
                       <span>{v.p_size}</span>
                      <br />
                      
                      <br />
                      1~2
                    </h5>
                  ))}
                </div>
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <Accordion.Toggle as={Card.Header} eventKey="1">
              <div className="info-head d-flex justify-content-between d-flex align-items-center">
                <h5 className="k-note"></h5>
                <MdAdd />
              </div>
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="1">
              <Card.Body>
                <div className="info-body">
                  <ul className="k-ul-note">
                    <li className="note-list">
                      $185$3,600
                    </li>
                    <li className="note-list">
                      
                    </li>
                    <li className="note-list">
                      2
                    </li>
                    <li className="note-list">
                      
                    </li>
                    <li className="note-list">
                      /2
                    </li>
                  </ul>
                </div>
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>
      </div>
      <div className="more-info">
        <h4 className="more container"></h4>

        <CarouselShow />
      </div>

      <div class="cursor2"></div>
    </>
  )
}

export default withRouter(MainProductDetail)
