import '../../styles/campaignIndex.scss'
import '../../styles/campaignIndexPhone.scss'
import { withRouter } from 'react-router-dom'
import { Container, Row, Col } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { fas } from '@fortawesome/free-solid-svg-icons'
import DisplayCampaignCardsIndex from '../../components/DisplayCampaignCardsIndex'
import indexVideo from '../../styles/img/indexVideo.mp4'
import { useEffect } from 'react'
import '../../styles/fontstyle.scss'
import $ from 'jquery'

function CampaignIndex(props) {
  const { searchCampaign, setSearchCampaign } = props
  const { setCategoryActiveObj } = props

  function enterCategory() {
    props.history.push('/campaign/searchProducts')
  }

  function search() {
    props.history.push('/campaign/searchProducts')
  }

  useEffect(() => {
    let curs = document.querySelector('.cursorFinn')

    document.addEventListener('mousemove', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      curs.style.top = y - 22 + 'px'
      // console.log('123')
    })

    document.addEventListener('mouseleave', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      // curs.style.opacity = 0.5
      curs.style.top = y - 22 + 'px'
    })
  }, [])

  useEffect(() => {
    $(window).on('scroll', function () {
      let scrollTop = $(this).scrollTop()
      $('.goldenRing').css('transform', 'translateY(-' + scrollTop / 8 + 'px)')
      $('.goldenRing2').css('transform', 'translateY(-' + scrollTop / 8 + 'px)')
    })
  }, [])

  useEffect(() => {
    $(window).scroll(function () {
      if ($(window).scrollTop() < 500) {
        $('.searchTitle').addClass('animate__fadeIn animate__slow')
      }
      if ($(window).scrollTop() > 400) {
        $('.searchTitle').removeClass('animate__fadeIn animate__slow')

        $('.tastingContent').addClass('animate__fadeInLeft animate__slow')
        $('.tastingBg').addClass('animate__fadeInLeft animate__slow')
        $('.tastingImg').addClass('animate__fadeInRight animate__slow')
      }
      if ($(window).scrollTop() < 400) {
        $('.tastingContent').removeClass('animate__fadeInLeft animate__slow')
        $('.tastingBg').removeClass('animate__fadeInLeft animate__slow')
        $('.tastingImg').removeClass('animate__fadeInRight animate__slow')
      }
      if ($(window).scrollTop() > 1500) {
        $('.tastingContent').removeClass('animate__fadeInLeft animate__slow')
        $('.tastingBg').removeClass('animate__fadeInLeft animate__slow')
        $('.tastingImg').removeClass('animate__fadeInRight animate__slow')
      }
      if ($(window).scrollTop() > 1100) {
        $('.experientContent').addClass('animate__fadeInRight animate__slow')
        $('.experientBg').addClass('animate__fadeInRight animate__slow')
        $('.experientImg').addClass('animate__fadeInLeft animate__slow')
      }
      if ($(window).scrollTop() < 1100) {
        $('.experientContent').removeClass('animate__fadeInRight animate__slow')
        $('.experientBg').removeClass('animate__fadeInRight animate__slow')
        $('.experientImg').removeClass('animate__fadeInLeft animate__slow')
      }
      if ($(window).scrollTop() > 2300) {
        $('.experientContent').removeClass('animate__fadeInRight animate__slow')
        $('.experientBg').removeClass('animate__fadeInRight animate__slow')
        $('.experientImg').removeClass('animate__fadeInLeft animate__slow')
      }
      if ($(window).scrollTop() > 1900) {
        $('.workshopContent').addClass('animate__fadeInLeft animate__slow')
        $('.workshopBg').addClass('animate__fadeInLeft animate__slow')
        $('.workshopImg').addClass('animate__fadeInRight animate__slow')
      }
      if ($(window).scrollTop() < 1900) {
        $('.workshopContent').removeClass('animate__fadeInLeft animate__slow')
        $('.workshopBg').removeClass('animate__fadeInLeft animate__slow')
        $('.workshopImg').removeClass('animate__fadeInRight animate__slow')
      } else {
      }
    })
  }, [])

  return (
    <>
      <div className="cursorFinn d-none d-sm-block"></div>
      <Container fluid className="callToAction">
        <Row className="position-relative d-flex justify-content-center mb-3">
          <div className="box">
            <video id="myVideo" autoPlay muted loop>
              <source src={indexVideo} type="video/mp4" />
            </video>
          </div>
          <Col
            lg={6}
            md={8}
            sm={10}
            xs={12}
            className="position-absolute
            h-100
            d-flex align-items-center"
          >
            <div className="searchBox mx-auto w-100">
              <div className="searchTitle animate__animated animate__fadeIn animate__slow">
                <h2 className="searchTitleText"></h2>

                <p></p>
              </div>
              <div
                className="searchBar w-100 d-flex
           justify-content-center "
              >
                <input
                  type="text"
                  name="campaignSearch"
                  id="campaignSearch"
                  placeholder="..."
                  value={searchCampaign}
                  onChange={(event) => {
                    setSearchCampaign(event.target.value)
                  }}
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') search()
                  }}
                />
                <button
                  className="searchBtn"
                  type="submit"
                  onClick={() => {
                    search()
                    setCategoryActiveObj({
                      categoryBtn1: false,
                      categoryBtn2: false,
                      categoryBtn3: false,
                      categoryBtn4: true,
                    })
                  }}
                >
                  <i className="fas fa-search" />
                  <FontAwesomeIcon icon={fas.faSearch} />
                  &ensp;
                </button>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
      {/*  */}
      <Container className="d-none d-sm-block">
        <Row>
          <Col lg={12} md={12} sm={12} xs={12} className="mb-2">
            <h2 className="hitTitle pl-3"></h2>
          </Col>
        </Row>
      </Container>
      {/*  */}
      <Container fluid className="d-block d-sm-none">
        <Row>
          <Col
            lg={12}
            xs={12}
            className="d-flex 
            justify-content-center mb-2"
          >
            <h2 className="hitTitle"></h2>
          </Col>
        </Row>
      </Container>
      {/*  */}
      <Container className="displayCampaignCardsBox animate__animated animate__slideInRight">
        <Row className="mb-3">
          <Col lg={12} md={12} sm={12} xs={12}>
            <DisplayCampaignCardsIndex />
          </Col>
        </Row>
      </Container>

      <Container fluid className="d-none d-sm-none d-md-none d-lg-block">
        <Row>
          <Col lg={12} md={12}>
            <div className="bgImg"></div>
          </Col>
        </Row>
      </Container>
      <Container
        fluid
        className="tastingIntro d-none d-sm-none d-md-none d-lg-block position-relative mb-5"
      >
        <Row className="h-100">
          <Col
            xl={7}
            lg={7}
            className="tastingBg d-flex align-items-center animate__animated"
          >
            <Col xl={2} lg={1}></Col>
            <Col xl={5} lg={6} className="tastingContent animate__animated ">
              <h2 className="tastingTitle mb-4"></h2>
              <p className="tastingText">
                
                <br />
                
                <br />
                
              </p>
              <p
                className="btn k_btn"
                onClick={() => {
                  enterCategory()
                  setCategoryActiveObj({
                    categoryBtn1: true,
                    categoryBtn2: false,
                    categoryBtn3: false,
                    categoryBtn4: false,
                  })
                }}
              >
                
                <span className="btn-style" />
              </p>
            </Col>
          </Col>
          <Col
            xl={7}
            lg={7}
            className="tastingImg position-absolute animate__animated"
          ></Col>
        </Row>
      </Container>

      <Container className="cardPhone d-xl-none d-lg-none mb-3">
        <Row className="phoneBg">
          <div className="col-12">
            <div className="phoneImg1 mb-3 "> </div>
            <div className="tastingContent px-3">
              <div className="spacingPusher"></div>
              <h2 className="tastingTitle"> </h2>
              <p className="tastingText">
                
                <br />
                
                <br />
                
              </p>
              <div className="w-100">
                <p
                  className="btn k_btn d-flex justify-content-center"
                  onClick={() => {
                    enterCategory()
                    setCategoryActiveObj({
                      categoryBtn1: true,
                      categoryBtn2: false,
                      categoryBtn3: false,
                      categoryBtn4: false,
                    })
                  }}
                >
                  
                  <span className="btn-style" />
                </p>
              </div>
            </div>
          </div>
        </Row>
      </Container>

      <Container fluid className="d-none d-sm-none d-md-none d-lg-block">
        <Row className="position-relative slideArea">
          <div className="goldenRing"></div>
        </Row>
      </Container>
      <Container
        fluid
        className="experienceIntro d-none d-sm-none d-md-none d-lg-block position-relative mb-5"
      >
        <Row className="h-100">
          <Col lg={5}></Col>
          <Col
            lg={7}
            className="experientBg d-flex align-items-center animate__animated
            "
          >
            <Col lg={5}></Col>
            <Col lg={5} className="experientContent animate__animated">
              <h2 className="tastingTitle mb-4"></h2>
              <p className="tastingText">
                DIY
                <br />
                
                <br />
                
              </p>
              <p
                className="btn k_btn"
                onClick={() => {
                  enterCategory()
                  setCategoryActiveObj({
                    categoryBtn1: false,
                    categoryBtn2: true,
                    categoryBtn3: false,
                    categoryBtn4: false,
                  })
                }}
              >
                
                <span className="btn-style" />
              </p>
            </Col>
          </Col>
          <Col
            lg={7}
            className="experientImg position-absolute animate__animated"
          ></Col>
        </Row>
      </Container>
      <Container className="cardPhone d-xl-none d-lg-none">
        <Row className="phoneBg">
          <div className="col-12">
            <div className="phoneImg2 mb-3 "> </div>
            <div className="tastingContent px-3">
              <div className="spacingPusher"></div>
              <h2 className="tastingTitle"></h2>
              <p className="tastingText">
                DIY
                <br />
                
                <br />
                
              </p>
              <div className="w-100">
                <p
                  className="btn k_btn d-flex justify-content-center"
                  onClick={() => {
                    enterCategory()
                    setCategoryActiveObj({
                      categoryBtn1: true,
                      categoryBtn2: false,
                      categoryBtn3: false,
                      categoryBtn4: false,
                    })
                  }}
                >
                  
                  <span className="btn-style" />
                </p>
              </div>
            </div>
          </div>
        </Row>
      </Container>
      <Container fluid className="d-none d-sm-none d-md-none d-lg-block">
        <Row className="position-relative">
          <div className="goldenRing2"></div>
        </Row>
      </Container>
      <Container
        fluid
        className="workshopIntro d-none d-sm-none d-md-none d-lg-block position-relative mb-5"
      >
        <Row className="h-100">
          <Col
            lg={7}
            className="workshopBg d-flex align-items-center animate__animated"
          >
            <Col lg={2}></Col>
            <Col lg={5} className="workshopContent animate__animated">
              <h2 className="tastingTitle mb-4"></h2>
              <p className="tastingText">
                
                <br />
                
                <br />
                
              </p>
              <p
                className="btn k_btn"
                onClick={() => {
                  enterCategory()
                  setCategoryActiveObj({
                    categoryBtn1: false,
                    categoryBtn2: false,
                    categoryBtn3: true,
                    categoryBtn4: false,
                  })
                }}
              >
                
                <span className="btn-style" />
              </p>
            </Col>
          </Col>
          <Col
            lg={7}
            className="workshopImg position-absolute animate__animated"
          ></Col>
        </Row>
      </Container>
      <Container className="cardPhone d-xl-none d-lg-none">
        <Row className="phoneBg">
          <div className="col-12">
            <div className="phoneImg3 mb-3 "> </div>
            <div className="tastingContent px-3">
              <div className="spacingPusher"></div>
              <h2 className="tastingTitle"></h2>
              <p className="tastingText">
                
                <br />
                
                <br />
                
              </p>
              <div className="w-100">
                <p
                  className="btn k_btn d-flex justify-content-center"
                  onClick={() => {
                    enterCategory()
                    setCategoryActiveObj({
                      categoryBtn1: true,
                      categoryBtn2: false,
                      categoryBtn3: false,
                      categoryBtn4: false,
                    })
                  }}
                >
                  
                  <span className="btn-style" />
                </p>
              </div>
            </div>
          </div>
        </Row>
      </Container>
    </>
  )
}

export default withRouter(CampaignIndex)
