import '../../styles/MainProduct.scss'
import '../../styles/font.scss'
import { MdSearch } from 'react-icons/md'
import { Link } from 'react-router-dom'
import { MdKeyboardArrowRight } from 'react-icons/md'
import { MdKeyboardArrowLeft } from 'react-icons/md'
import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'

function MainProduct(props) {
  const [product, setProduct] = useState([])
  const [page, setPage] = useState([])
  const [totalPages, setTotalPages] = useState([])
  const [category, setCategory] = useState(null)

  // +
  //onClick functionlionClickfunctionp_catenowPage
  //const doCate = async (p_cate) => {
  const doCate = async (p_cate, nowPage) => {
    //setp_cate
    const url = `http://localhost:4000/mainproductcate2?page=${nowPage}&productCate=${p_cate}`
    const request = new Request(url, {
      method: 'GET',
      //body: JSON.stringify({ productCate: p_cate }),
      // body: JSON.stringify({ productCate: p_cate, page: nowPage }),
      // headers: new Headers({
      //   Accept: 'application/json',
      //   'Content-Type': 'application/json',
      // }),
    })
    const response = await fetch(request)
    const data = await response.json()
    // console.log(' : ', data)
    setProduct(data.rows)
    setTotalPages(data.totalPages)
    setPage(nowPage)
    setCategory(p_cate) //2p_cate
  }

  //node
  useEffect(() => {
    const FetchData = async () => {
      const url = 'http://localhost:4000/mainproductcate' //nodeapp.get('/mainproduct')
      const request = new Request(url, {
        method: 'POST',
        body: JSON.stringify({ productCate: 1 }), //
        headers: new Headers({
          Accept: 'application/json',
          'Content-Type': 'application/json',
        }),
      })
      const response = await fetch(request)
      const rows = await response.json() //rows
      console.log('', rows) //console.logrows

      setProduct(rows) //rowsproductsetXXX
    }
    let curs = document.querySelector('.cursor2')

    document.addEventListener('mousemove', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      curs.style.top = y - 22 + 'px'
      // console.log('123')
    })

    document.addEventListener('mouseleave', (e) => {
      let x = e.pageX
      let y = e.pageY
      curs.style.left = x - 22 + 'px'
      // curs.style.opacity = 0.5
      curs.style.top = y - 22 + 'px'
    })
    FetchData()
  }, []) //

  const productDisplay = (
    <>
      <ul className="k-main-products row">
        {product.map((v, i) => (
          <li key={i} className="col-lg-4 col-sm-12 k-product-card card">
            <div className="k-img-box">
              <Link to={`/mainproductdetail/products/` + v.p_sid}>
                <img
                  src={`http://localhost:3000/k-images/` + v.p_img}
                  className="card-img-top product-img"
                  alt="cake29"
                />
              </Link>
            </div>

            <div className="card-body">
              <h5 className="card-title">{v.p_name}</h5>
              <p className="card-text">{v.p_intro}</p>
              <hr className="product-hr" />
              <div className="k-product-price">$ {v.p_price}</div>
            </div>
          </li>
        ))}
      </ul>
    </>
  )

  //

  let items = []
  for (let number = 1; number <= totalPages; number++) {
    items.push(
      <li
        onClick={() => {
          doCate(category, number)
          //categorynumber()numberdoCate()const [category, setCategory] = useState(null)
        }}
        className="page-item"
      >
        <Link className="page-link hoyu-page-link" to={`?page=${number}`}>
          {number}
        </Link>
      </li>
    )
    // V1.react bspaginationitems.push(<Pagination.Item key={number}>{number}</Pagination.Item>)
    //V2.react bsitems.push(<li className="page-item"><Link className="page-link hoyu-page-link" to="#"}>{number}</Link></li>)
    // push<Pagination.Item key={number} active={number === active}>{number}</Pagination.Item>items[]
  }

  return (
    <>
      <div class="cursor2"></div>

      <div className="content">
        <div className="k-hero-img">
          <div className="k-product-desc">
            <h3 className="subtitle"></h3>
            <h2 className="k-title"></h2>
            <p className="k-description"></p>
          </div>
        </div>

        <div className="k-main container">
          <div className="row k-upper">
            <div className="col-lg-6 col-sm-12 k-breadcrumbs">
              <Link className="clbread" to="/MainPage">
                {'  '}
                <span className="clspan">{'>'}</span>
              </Link>
              <Link className="clbread" to="/mainproduct">
                {'  '}
                <span className="clspan">{'>'}</span>
              </Link>
              <Link className="clbread" to="/mainproduct">
                
              </Link>
            </div>
            <div className="col-lg-6 col-sm-12 search-input">
              {/* <form className="form-inline my-2 d-flex justify-content-end">
                <input
                  className="form-control"
                  type="search"
                  placeholder=""
                  aria-label="Search"
                />
                <button
                  className="btn btn-outline-success product-search-btn my-2 my-sm-0"
                  type="submit"
                >
                  <MdSearch />
                </button>
              </form> */}
            </div>
          </div>
          <div className="k-product-category">
            <ul className="k-ul d-flex justify-content-between">
              <li
                className="k-category"
                onClick={() => {
                  doCate(1, 1)
                  // setCategory(1)
                  // console.log('kll')
                }}
              >
                <span></span>
              </li>
              <li
                className="k-category"
                onClick={() => {
                  doCate(2, 1) //2
                  //1setCategory(2)2number22(2)setCategorycategoryonClickdoCateli
                }}
              >
                <span></span>
              </li>
              <li
                className="k-category"
                onClick={() => {
                  doCate(3, 1)
                }}
              >
                <span></span>
              </li>
              <li
                className="k-category"
                onClick={() => {
                  doCate(4, 1)
                }}
              >
                <span></span>
              </li>
              <li
                className="k-category"
                onClick={() => {
                  doCate(5, 1)
                }}
              >
                <span></span>
              </li>
            </ul>
          </div>

          {productDisplay}
          {/* <ul className="k-main-products row">  li
            <li className="col-lg-4 col-sm-12 k-product-card card">
              <div className="k-img-box">
                <Link to="#">
                  <img
                    src="http://localhost:3000/k-images/1.jpeg"
                    className="card-img-top product-img"
                    alt="cake29"
                  />
                </Link>
              </div>

              <div className="card-body">
                <h5 className="card-title"></h5>
                
                <p className="card-text">
                  ,
                  
                </p>
                <hr className="product-hr" />
                <div className="k-product-price">$ 450</div>
              </div>
            </li>
            
          </ul> */}
          <nav
            aria-label="Page navigation example"
            className="hoyu-mt d-flex justify-content-end"
          >
            <ul className="pagination justify-content-center">
              <li className="page-item">
                <Link
                  className="page-link hoyu-page-link hoyu-page-arrow"
                  to="#"
                  aria-label="Previous"
                >
                  <span aria-hidden="true">
                    <MdKeyboardArrowLeft />
                  </span>
                </Link>
              </li>

              {items}

              <li className="page-item">
                <Link
                  className="page-link hoyu-page-link hoyu-page-arrow"
                  to="#"
                  aria-label="Next"
                >
                  <span aria-hidden="true">
                    <MdKeyboardArrowRight />
                  </span>
                </Link>
              </li>
            </ul>
          </nav>

          <div className="k-golden-design"></div>
        </div>
      </div>
    </>
  )
}

export default withRouter(MainProduct)
